<template>
  <div class="share-project-detail full-screen">
    <div class="page-panel">
      <pem-scroll-view class="scroll-view" ref="scroll">
        <div class="container" @click="judgeGuide">
          <div class="header-open">
            <img class="logo" src="http://m.pemarket.com.cn/static/logo.png" alt="">
            <h4>项目工场</h4>
            <p>找项目、找投资就到项目工场</p>
            <div class="btn">打开</div>
          </div>
          <div class="inner">
            <div class="project-msgs">
              <img class="project-logo" :src="basic.logo" alt=""  @error="defaultLogo">
              <h3 class="project-name">{{ basic.name }}</h3>
              <div class="field-address">
                <span class="project-field">{{ basic.fields }}</span>
                <small>|</small>
                <span class="project-address">{{ basic.address }}</span>
              </div>
              <div class="project-desc">{{ basic.descriptionmemo }}</div>
              <div class="turn-money-ratio">
                <span class="project-turn" v-if="basic.turn">{{ basic.turn }}</span>
                <span class="project-money" v-if="basic.money">{{ basic.money }}</span>
                <span class="project-ratio" v-if="basic.stockratio1">{{ basic.stockratio1 }}</span>
              </div>
            </div>
            <div v-if="basic.highlightsmemo">
              <div class="basic-title"><span class="title">项目亮点</span></div>
              <div class="basic-desc">{{ basic.highlightsmemo }}</div>
            </div>
            <div v-if="basic.corporatename">
              <div class="basic-title"><span class="title">公司全称</span></div>
              <div class="basic-desc">{{ basic.corporatename }}</div>
            </div>
            <div v-if="basic.summarymemo">
              <div class="basic-title"><span class="title">公司简介</span></div>
              <div class="basic-desc">{{ basic.summarymemo }}</div>
            </div>
            <div v-if="teamArr && teamArr.length > 0">
              <div class="basic-title"><span class="title">团队成员</span></div>
              <ul class="team-list">
                <li class="team-item" v-for="(teamItem, index) in teamArr" :key="index">
                  <img class="logo" :src="teamItem.stafflogo" alt="" @error="defaultLogo($event, 'team')">
                  <div class="msgs">
                    <span class="name">{{ teamItem.staffname }}</span>
                    <span class="job">{{ teamItem.staffposition }}</span>
                  </div>
                  <div class="introduce">{{ teamItem.staffmemo }}</div>
                </li>
              </ul>
            </div>
            <div v-if="financeArr && financeArr.length > 0">
              <div class="basic-title"><span class="title">融资历史</span></div>
              <ul class="history-list">
                <li class="history-item"  v-for="(historyItem, index) in financeArr" :key="index">
                  <div class="turn-money">
                    <span class="turn">{{ historyItem.turn }}</span>
                    <span class="money">{{ historyItem.money }}</span>
                  </div>
                  <div class="time-company">
                    <span class="time">{{ historyItem.time }}</span>
                    <span class="company">{{ historyItem.company }}</span>
                  </div>
                </li>
              </ul>
            </div>
            <div v-if="business.name || business.regNumber || business.regCapital || business.regLocation || business.estiblishTime || business.legalPersonName">
              <div class="basic-title clearfix">
                <span class="title">工商信息</span>
                <span class="float-right seemore">查看更多</span>
              </div>
              <ul class="business-list">
                <li class="business-item" v-if="business.name">
                  <span class="key">公司名称：</span>
                  <span class="value">{{ business.name }}</span>
                </li>
                <li class="business-item" v-if="business.regNumber">
                  <span class="key">工商注册：</span>
                  <span class="value">{{ business.regNumber }}</span>
                </li>
                <li class="business-item" v-if="business.regCapital">
                  <span class="key">注册资本：</span>
                  <span class="value">{{ business.regCapital }}</span>
                </li>
                <li class="business-item" v-if="business.regLocation">
                  <span class="key">注册地址：</span>
                  <span class="value">{{ business.regLocation }}</span>
                </li>
                <li class="business-item" v-if="business.estiblishTime">
                  <span class="key">成立时间：</span>
                  <span class="value">{{ business.estiblishTime }}</span>
                </li>
                <li class="business-item" v-if="business.legalPersonName">
                  <span class="key">法人代表：</span>
                  <span class="value">{{ business.legalPersonName }}</span>
                </li>
              </ul>
            </div>
            <div v-if="commentArr && commentArr.length > 0">
              <div class="basic-title">
                <span class="title">项目点评</span>
                <span class="score" v-if="commentScore">(综合评分{{ commentScore }})</span>
                <span class="float-right seemore" v-if="commentCount > 3">查看全部</span>
              </div>
              <ul class="comment-list">
                <li class="comment-item" v-for="(item, index) in commentArr" :key="index">
                  <img class="logo" :src="item.logo" alt="" @error="defaultLogo($event, 'comment')">
                  <div class="msgs">
                    <div class="clearfix name-wrapper">
                      <span class="name">{{ item.name }}</span>
                      <span class="float-right time">{{ item.time }}</span>
                    </div>
                    <div class="scores">
                      <span>打分</span>
                      <img v-for="star in item.score" :key="star" src="./star_on@2x.png" alt="">
                      <img v-for="star in (5 - item.score)" :key="star" src="./star@2x.png" alt="">
                    </div>
                    <div class="comment-content">{{ item.remark }}</div>
                  </div>
                </li>
              </ul>
            </div>
            <div v-if="similarProjectArr && similarProjectArr.length > 0">
              <div class="basic-title"><span class="title">相似项目</span></div>
              <ul class="similar-project-list">
                <li class="similar-project-item" v-for="(item, index) in similarProjectArr" :key="index">
                  <img class="logo" :src="item.logo" alt="" @error="defaultLogo">
                  <div class="msg-wrapper">
                    <div class="name">{{ item.name }}</div>
                    <div class="msgs">
                      <span class="turn">{{ item.turn }}</span>
                      <small v-if="item.money">|</small>
                      <span class="money" v-if="item.money">{{ item.money }}</span>
                      <small v-if="item.address">|</small>
                      <span class="address" v-if="item.address">{{ item.address }}</span>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </pem-scroll-view>
      <div class="comment-close-btn" @click="judgeGuide"></div>
      <div class="comment-bar" @click="judgeGuide"></div>
      <div class="footer-wrapper" @click="judgeGuide">
        <div class="mark-btn">收藏</div>
        <div class="seebp-btn">查看BP</div>
        <div class="contact-btn">联系项目方</div>
      </div>
      <div class="guide-open-browser" v-if="showGuideMask" @click="closeMask">
        <div class="guide-img"></div>
      </div>
    </div>
    <pem-toast ref="toast" :text="toastMsg"></pem-toast>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import PemButton from 'components/pem-button/pem-button'
import PemToast from 'components/pem-toast/pem-toast'
import { MiniSiteApi, ImgPath } from 'api'
import { setTitle, device, dateFormat } from 'common/js/util'

export default {
  data () {
    return {
      toastMsg: '',
      showGuideMask: false,
      basic: {
        logo: 'http://cdn.xcx.pemarket.com.cn/img_logo_44x44_default.png'
      },
      teamArr: [],
      financeArr: [],
      business: {},
      commentScore: 0,
      commentCount: 0,
      commentArr: [],
      similarProjectArr: []
    }
  },
  components: {
    PemScrollView,
    PemButton,
    PemToast
  },
  created () {
    this._initData()
    this.wxShare({})
  },
  methods: {
    _initData () {
      if (!device.isWeixin() && device.isAndroid()) {
        window.location.href = `pemarket://www.pemarket.com.cn/attr?type=5&id=${this.$route.query.id}`
      }
      this._getShareSpecial()
    },
    _getShareSpecial () {
      let options = {
        position: 'shareproject',
        zj: this.$route.query.id
      }
      MiniSiteApi.getAppShareProject(options).then(data => {
        if (+data.resultcode === 1) {
          this.basic.logo = data.basic.logo ? (ImgPath + data.basic.logo) : 'http://cdn.xcx.pemarket.com.cn/img_logo_44x44_default.png'
          this.basic.name = data.basic.name
          setTitle(this.basic.name)
          this.wxShare({
            title: this.basic.name,
            content: `我在项目工场上看到一个有趣的项目 ${this.basic.name},发给你一起看看吧`,
            imgUrl: this.basic.logo
          })
          this.basic.address = data.basic.address
          this.basic.descriptionmemo = data.basic.descriptionmemo
          this.basic.fields = (data.field || []).map(item => {
            return item.fieldname
          }).join('、')
          this.basic.turn = data.basic.turn
          this.basic.money = '暂不透露'
          if (!data.basic.raisefundstatus && data.basic.raisefunds && data.basic.currencytype && data.basic.raisefunds > 0) {
            this.basic.money = data.basic.currencytype + data.basic.raisefunds + '万'
          }
          this.basic.stockratio1 = '暂不透露'
          if (!data.basic.stockratiostatus && data.basic.stockratio1 && data.basic.stockratio1 > 0) {
            this.basic.stockratio1 = data.basic.stockratio1 + '%'
          }
          this.basic.highlightsmemo = data.basic.highlightsmemo
          this.basic.corporatename = data.basic.corporatename
          this.basic.summarymemo = data.basic.summarymemo
          this.teamArr = (data.staffs || []).map(item => {
            let teamMember = {}
            teamMember.staffname = item.staffname
            teamMember.staffposition = item.staffposition
            teamMember.staffmemo = item.staffmemo
            teamMember.stafflogo = item.stafflogo ? (ImgPath + item.stafflogo) : 'http://cdn.xcx.pemarket.com.cn/team_kong@2x.png'
            return teamMember
          })
          this.financeArr = (data.histroyList || []).map(item => {
            let history = {}
            history.turn = item.turnzj
            history.time = dateFormat(Number(item.raisetime), 'yyyy-MM')
            history.company = item.financingside
            history.money = item.currencytype + item.raisefunds + '万'
            return history
          })
          this.business = data.tianyanchaCompany || {}
          if (data.tianyanchaCompany && data.tianyanchaCompany.estiblishTime) {
            this.business.estiblishTime = dateFormat(Number(data.tianyanchaCompany.estiblishTime), 'yyyy-MM')
          }
        } else {
          this.toastMsg = '网络请求失败!'
          this.$refs.toast.show()
        }
      })
      MiniSiteApi.getAppShareProjectComment({
        position: 'detail',
        pageSize: '3',
        currentPage: '1',
        projectzj: this.$route.query.id
      }).then(data => {
        this.commentScore = data.score || null
        this.commentCount = data.totalCount || 0
        this.commentArr = (data.resultList || []).map(item => {
          let commentItem = {}
          commentItem.logo = item.userlogo ? (ImgPath + item.userlogo) : 'http://cdn.xcx.pemarket.com.cn/icon-Default%20head%20image@2x.png'
          commentItem.name = item.name
          commentItem.score = +item.score || 0
          commentItem.remark = item.remark
          commentItem.time = item.time
          return commentItem
        })
      })
      MiniSiteApi.getAppShareProjectSimilar({projectzj: this.$route.query.id}).then(data => {
        if (+data.resultcode === 1) {
          this.similarProjectArr = (data.resultList || []).map(item => {
            let similarItem = {}
            similarItem.logo = item.logo ? (ImgPath + item.logo) : 'http://cdn.xcx.pemarket.com.cn/img_logo_44x44_default.png'
            similarItem.name = item.name
            similarItem.turn = item.turn || null
            similarItem.address = item.address || null
            similarItem.money = '暂不透露'
            if (!item.raisefundstatus && item.raisefunds && item.currencytype && item.raisefunds > 0) {
              similarItem.money = item.currencytype + item.raisefunds + '万'
            }
            return similarItem
          })
        }
      })
    },
    judgeGuide () {
      if (device.isWeixin()) {
        if (device.isIOS()) {
          window.location.href = `https://go.pemarket.com.cn/download-guide?type=5&id=${this.$route.query.id}`
        } else {
          this.showGuideMask = true
        }
      } else {
        this.$router.push('/download')
      }
    },
    closeMask () {
      this.showGuideMask = false
    },
    refresh () {
      this.$refs.scroll.refresh()
    },
    defaultLogo (e, type = 'project') {
      let defaultsrc = {
        project: 'http://cdn.xcx.pemarket.com.cn/img_logo_44x44_default.png',
        team: 'http://cdn.pemarket.com.cn/default_investor_logo@2x.png',
        comment: 'http://cdn.xcx.pemarket.com.cn/icon-Default%20head%20image@2x.png'
      }[type]
      e.target.src = defaultsrc
    }
  }
}
</script>

