          .similar-project-item
            position: relative
            padding: 0.3rem 0 0 1.08rem
            &:last-child
              .msg-wrapper
                border-bottom: none
            .logo
              position: absolute
              left: 0
              top: 0.3rem
              width: 0.88rem
              height: 0.88rem
              border-radius: 50%
            .msg-wrapper
              padding-bottom: 0.3rem
              border-bottom: 0.5px solid #f3f3f3
              .name
                font-family: PingFangSC-Medium
                font-size: 0.32rem
                font-weight: bold
                color: #333333;
                letter-spacing: -0.26px;
                line-height: 0.42rem
              .msgs
                font-size: 0
                padding-top: 0.1rem
                span
                  font-family: PingFangSC-Regular;
                  font-size: 0.24rem
                  color: #666666;
                  letter-spacing: -0.07px;
                  line-height: 0.34rem
                small 
                 font-size: 0.2rem
                 color: #c9c9c9
                 line-height: 0.34rem
                 margin: 0 0.1rem
                 vertical-align: super
</style>


